import './styles.css';
import ApiService from './js/apiService';
import CREATE from './js/createDomElements';
import galleryItemTpl from './templates/gallery.hbs';


const btnLoadMore = document.querySelector('.loadMoreBtn');

const apiService = new ApiService();

btnLoadMore.addEventListener('click', findImages)
CREATE.createSearchForm(onSearch);
CREATE.createGallery();
const galleryListRef = document.querySelector(".gallery");

function onSearch(event) {
    event.preventDefault();
    
    apiService.query = event.currentTarget.elements.query.value;
    if (apiService.query === "" || apiService.query === " ") {
        btnLoadMore.classList.replace('is-visible', 'is-hidden')
        return alert("Please enter You would like to see!");
    }
    btnLoadMore.classList.replace('is-hidden', 'is-visible');
    apiService.resetPage();
    clearGallery();
    // console.log(`We are finding ${apiService.query}!`);
    findImages();
}

function findImages() {
    apiService.fetchImages()
        .then(images => {
            console.log(images);
            renderImages(images);
            // window.scrollTo({
            //     top: 100,
            //     left: 100,
            //     behavior: 'smooth'
            // });
        });
}
        
function renderImages(item) {
    // const galleryListRef = document.querySelector(".gallery");
    // console.log(galleryListRef);
    const galleruItemMarkup = galleryItemTpl(item);
    galleryListRef.insertAdjacentHTML('beforeend', galleruItemMarkup);

}

function clearGallery() {
    
    galleryListRef.innerHTML = '';
}






