const bodyRef = document.querySelector('body');
// console.log(bodyRef);

function createSearchForm(onBtnSubmit) {
    bodyRef.insertAdjacentHTML('beforebegin', '<form class="search-form" id="search-form"><input type="text" name = "query" autocomplete="off" placeholder="Search images..."/> <button type="submit" class="submit-btn">Submit</button> </form>')
    const formRef = document.querySelector('#search-form');
    formRef.addEventListener('submit', onBtnSubmit)
}

function createGallery() {
    bodyRef.insertAdjacentHTML('afterbegin', '<ul class="gallery"></ul>')
}

export default { createSearchForm, createGallery };

